import React, { useState, useEffect } from 'react';
import { Button, Grid, Modal, Box } from '@mui/material';
import { Add, Remove } from '@mui/icons-material';
import { ROLE_INFO, DEFAULT_DESK } from '../constant';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  align: 'center',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};
export const Roles = (props) => {
  const { roles, sum, setRoles } = props;
  const [infoModal, setInfoModal] = useState(false);
  const [currentRole, setCurrentRole] = useState({ key: 'villager' });
  const [desk, setDesk] = useState(DEFAULT_DESK);

  useEffect(() => {
    setRoles(desk.filter((d) => d.number > 0));
  }, [desk]);

  const point = () => {
    let point = 0;
    roles.map((role) => {
      point = point + role.point * role.number;
    });

    return point;
  };

  const update = (type, key, value) => {
    setDesk(desk.map((role) => {
      const newRole = role;
      if (role.key == type) {
        newRole[key] = value;
      }
      return newRole;
    }));
  };
  return (
    <>

      <Modal
        open={infoModal}
        onClose={() => setInfoModal(false)}
      >
        <Box sx={{ ...style, width: 300 }}>
          <h1>{currentRole.name}</h1>

          <img
            src={'/werewolf_icon/black/' + currentRole.key + '.png'}
            height={50}
          />
          <p>{ROLE_INFO.find((e) => e.key === currentRole.key).text}</p>

        </Box>
      </Modal>

      <h3>Roles: {sum()}({point()})</h3>
      {desk.map((role, index) => (
        <Grid
          key={index}
          sx={{
            padding: 1,
          }}

        >

          <Grid
            sx={{
              borderStyle: 'solid',
              boder: 1,
              borderColoer: 'black',
              height: 60,
              position: 'relative',
              margin: 0,
            }}
            container
            spacing={0}
          >
            <Grid
              item
              xs={2}
              onClick={() => {
                setCurrentRole(role);
                setInfoModal(true);
              }}
            >
              <img
                src={'/werewolf_icon/black/' + role.key + '.png'}
                height={50}
              />
            </Grid>
            <Grid item xs={5}>
              <h3>{role.name}</h3>
            </Grid>
            <Grid item xs={2} align="center">
              <Button
                variant="outlined"
                style={{
                  marginTop: 9,
                  padding: 5,
                  minWidth: 50,
                }}
                disabled={role.number === 0}
                onClick={() => {
                  update(role.key, 'number', role.number-1);
                }}
              >
                <Remove/>
              </Button>
            </Grid>
            <Grid
              item
              xs={1}
              sx={{
                padding: 1,
                align: 'center',
                marginTop: 1,
              }}
            >
              {role.number}
            </Grid>

            <Grid item xs={2} align="center">
              <Button
                variant="outlined"
                style={{
                  marginTop: 9,
                  padding: 5,
                  minWidth: 50,
                }}
                disabled={role.number === role.max}
                onClick={() => {
                  update(role.key, 'number', role.number+1);
                }}
              >
                <Add/>
              </Button>
            </Grid>
          </Grid>
        </Grid>
      ))}
    </>
  );
};
