import React, { useState, useEffect } from 'react';
import { Header } from './components/Header';
import { Picker } from './components/Picker';
import { DEFAULT_DESK, DEFAULT_LOG } from './constant';
import { CountdownTimer } from './components/Countdown';
import { Settings } from './Tabs/Settings';
import { Players } from './Tabs/Players';
import { Roles } from './Tabs/Roles';
import { Notes } from './Tabs/Notes';
import { NotiModal } from './Modals/NotiModal';
import { HunterModal } from './Modals/HunterModal';
import { collectActions, submitActions, actionHunt } from './util.js';
import {
  Button,
  Grid,
} from '@mui/material';

export default function WerewolfV3() {
  const getStorage = (name, value = null) => {
    const data = JSON.parse(localStorage.getItem('data'));
    if (data) {
      return data[name];
    } else {
      return value;
    }
  };
  const [tab, setTab] = useState('home');
  const [roles, setRoles] = useState(JSON.parse(localStorage.getItem('roles')) || DEFAULT_DESK.filter((r) => r.number > 0));
  const [gameInfo, setGameInfo] = useState(JSON.parse(localStorage.getItem('gameInfo')) || null);
  const [players, setPlayers] = useState(JSON.parse(localStorage.getItem('players')) || []);
  const [actions, setActions] = useState(JSON.parse(localStorage.getItem('actions')) || []);
  const [logs, setLogs] = useState(JSON.parse(localStorage.getItem('logs')) || []);
  const [noti, setNoti] = useState([]);
  const [hunter, setHunter] = useState([]);
  const [bitedSicker, setBitedSicker] = useState(getStorage('bitedSicker', false));

  useEffect(() => {
    localStorage.setItem('gameInfo', JSON.stringify(gameInfo));
    localStorage.setItem('roles', JSON.stringify(roles));
    localStorage.setItem('players', JSON.stringify(players));
    localStorage.setItem('actions', JSON.stringify(actions));
    localStorage.setItem('logs', JSON.stringify(logs));

    const data = {
      bitedSicker,
    };

    localStorage.setItem('data', JSON.stringify(data));
  }, [gameInfo, roles, players, actions, logs, bitedSicker]);

  useEffect(() => {
    if (gameInfo) {
      setActions(collectActions(roles, players, gameInfo, bitedSicker));
      if (!roles[gameInfo.turn].has_action && gameInfo.day !== 0) {
        nextTurn();
      }
    }
  }, [gameInfo, hunter]);

  const nextButton = () => {
    if (gameInfo === null) {
      // call when start game
      setGameInfo({ day: 0, turn: 0, winner: '' });
      setLogs([structuredClone(DEFAULT_LOG)]);
    } else {
      // call with later actions
      submitActions({
        gameInfo,
        setGameInfo,
        roles,
        actions,
        players,
        setPlayers,
        noti,
        setNoti,
        logs,
        setLogs,
        setHunter,
        setBitedSicker,
      });
      nextTurn();
    }
  };

  const nextTurn = () => {
    if (gameInfo.winner !== '') return;
    if (roles.length == gameInfo.turn + 1) {
      setGameInfo({ ...gameInfo, turn: 0, day: gameInfo.day + 1 });
      const newLog = logs;
      newLog.push(structuredClone(DEFAULT_LOG));
      setLogs(newLog);
    } else {
      setGameInfo({ ...gameInfo, turn: gameInfo.turn + 1 });
    }
  };

  // const backup = () => {
  //   const backupData = {
  //     gameInfo,
  //     roles,
  //     players,
  //     actions,
  //     logs,
  //   };
  //
  //   localStorage.setItem('backup', JSON.stringify(backupData));
  // };
  //
  // const restore = () => {
  //   const data = JSON.parse(localStorage.getItem('backup'));
  //   setGameInfo(data.gameInfo);
  //   setRoles(data.roles);
  //   setPlayers(data.players);
  //   setActions(data.actions);
  //   setLogs(data.logs);
  // };


  const sum = () => {
    let sum = 0;
    roles.map((role) => {
      sum = sum + role.number;
    });
    return sum;
  };

  return (
    <div className="main v2">
      <Header
        setTab={setTab}
        tab={tab}
      />
      <HunterModal
        hunter={hunter}
        actionHunt={(name) => {
          actionHunt(name, {
            players,
            setPlayers,
            noti,
            setNoti,
            gameInfo,
            setGameInfo,
            logs,
            setLogs,
          });
        }}
        setHunter={setHunter}
      />
      <CountdownTimer
        hidden={tab !== 'home'}
      />
      <NotiModal
        noti={noti}
        setNoti={setNoti}
      />

      {tab === 'notes' && gameInfo !== null &&
        <Notes
          logs={logs}
          players={players}
          gameInfo={gameInfo}
        />
      }
      {tab === 'home' &&
        <>
          {gameInfo === null &&
          <>
            <h3>Roles: {sum()}</h3>
            <h3>Players: {players.length}</h3>
          </>
          }
          {gameInfo !== null && actions.length > 0 &&
            <>
              <h1>
                {roles.find((r) => r.key === actions[0].role).name} i dy i
              </h1>
            </>
          }
          {actions.map((action) =>
            <div key={action.name}>
              {['grant', 'minion_report', 'lover', 'wolf_bite', 'lone_wolf_bite', 'indent', 'protect', 'poison', 'silent', 'vote', 'revive'].includes(action.name) &&
                <>
                  <h3>{action.values.length}/{action.number} {action.text}:</h3>
                  <Picker
                    key={action.key}
                    action={action}
                    name={action.name}
                    items={action.items}
                    values={action.values}
                    setActions={setActions}
                    actions={actions}
                    max={action.number}
                    disable={action.disable}
                  />
                </>
              }
              {action.name === 'cursed' &&
                <>
                  <h1>{action.values}</h1>
                </>
              }

            </div>,
          )}
          <Grid align="center">
            {gameInfo == null &&
              <Button
                onClick={nextButton}
                variant="outlined"
                disabled={players.length !== sum()}
              >
                Start
              </Button>
            }
            {gameInfo !== null &&
              <Button
                onClick={nextButton}
                variant="outlined"
              >
                Next
              </Button>
            }
          </Grid>
        </>
      }
      {tab === 'settings' &&
        <Settings
          resetGame={() => {
            setGameInfo(null);
            setLogs([]);
            setPlayers(players.map((p) => {
              p.note = '';
              p.role = '';
              p.status = true;
              p.lover = false;

              return p;
            }));
            setRoles(roles.map((role) => {
              if (role.key === 'witch') {
                role.poison = false;
                role.revive = false;
              }
              return role;
            }));
            setActions([]);
            setBitedSicker(false);
          }}
          players={players}
        />
      }
      {tab === 'players' &&
        <Players
          players={players}
          setPlayers={setPlayers}
          gameInfo={gameInfo}
        />
      }
      {tab === 'roles' &&
        <>
          <Roles
            gameInfo={gameInfo}
            sum={sum}
            roles={roles}
            setRoles={setRoles}
          />
        </>
      }
    </div>

  );
}
