import React from 'react';

import { Card } from './Card';
import { Mask } from './Mask';
export const Picker = (props) => {
  const { items, values, setActions, actions, max, disable, name } = props;
  const clickItem = (userName) => {
    if (values.includes(userName)) {
      setActions(actions.map((action) => {
        if (action.name === name) {
          action.values = action.values.filter((e) => e !== userName);
        }
        return action;
      }));
    } else {
      setActions(actions.map((action) => {
        if (action.name === name && action.values.length < max) {
          action.values = [...action.values, userName];
        }
        return action;
      }));
    }
  };
  return (
    <>
      {disable && <Mask />}
      {items.map((item) => (
        <Card
          text={item.name}
          onClick={() => clickItem(item.name)}
          disable={disable}
          key={item.name}
          icon={item.role}
          active={values.includes(item.name)}
          note={item.note}
          lover={item.lover}
        />
      ))}
    </>

  );
};
