import React, { useState } from 'react';
import { Modal } from 'antd';
import { Card } from '../components/Card';

export const HunterModal = (props) => {
  const { hunter, setHunter, actionHunt } = props;
  const [checked, setChecked] = useState(0);

  const close = () => {
    setHunter([]);
  };

  return (
    <Modal
      open={hunter.length > 0}
      onCancel={close}
      onOk={() => {
        if (checked !== 0) {
          actionHunt(checked);
        }
        close();
      }}
    >
      {hunter.map((item) =>
        <Card
          text={item.name}
          key={item.name}
          icon={item.role}
          active={item.name == checked}
          onClick={() => {
            setChecked(item.name);
          }}
        />,
      )

      }

      <Card
        text={'None'}
        key={0}
        icon={'none'}
        active={0 == checked}
        onClick={() => {
          setChecked(0);
        }}
      />


    </Modal>
  );
};
