export const collectActions = (roles, players, gameInfo, bitedSicker) => {
  const { day, turn } = gameInfo;
  const role = roles[turn];

  const actions = [];
  if (day == 0 && role.key !== 'villager') {
    const grant = {
      role: role.key,
      name: 'grant',
      number: role.number,
      items: players.filter((p) => p.role === ''),
      text: role.name,
      values: [],
    };
    actions.push(grant);
  }

  const roleAction = buildRoleAction(players, roles, gameInfo, bitedSicker);
  if (roleAction) actions.push(roleAction);

  return actions;
};

export const actionHunt = (name, params) => {
  const { players, setPlayers } = params;
  setPlayers(players.map((p) => {
    if (p.name === name) {
      p.status = false;
      newNoti(name + ' died by hunter', params);
      checkDieImpact(name, params);
    }
    return p;
  }));
};

const nextRole = (params) => {
  const { gameInfo, roles } = params;
  let nextTurn = gameInfo.turn;
  do {
    if (nextTurn == roles.length - 1) {
      nextTurn = 0;
    } else {
      nextTurn++;
    }
  } while (gameInfo.day !== 0 && !roles[nextTurn].has_action);

  return roles[nextTurn];
};


export const submitActions = (params) => {
  const { gameInfo, setGameInfo, actions, players, setPlayers, logs, setLogs, setBitedSicker } = params;
  const log = logs[gameInfo.day];
  actions.forEach((action) => {
    const { values, name } = action;
    if (values.length !== 0) {
      switch (name) {
        case 'grant':
          setPlayers(players.map((p) => {
            if (values.includes(p.name)) {
              p.role = action.role;
            }
            return p;
          }));
          break;
        case 'lover':
          setPlayers(players.map((p) => {
            if (values.includes(p.name)) {
              p.lover = true;
            }
            return p;
          }));
          break;
        case 'vote':
          values.forEach((voted) => {
            setPlayers(players.map( (p) => {
              if (p.name === voted) {
                p.status = false;
                newNoti(voted + ' died by vote', params);
                checkDieImpact(voted, params);
              }
              return p;
            }));
            if (players.find((p) => p.name === voted).role == 'tanner') {
              setGameInfo({ ...gameInfo, winner: 'tanner' });
              newNoti( 'Tanner win', params);
            }
            checkDieImpact(voted, params);
            log.voted = voted;
          });
          break;
        case 'wolf_bite':
          log.bited = values;
          break;

        default:
          log[name] = values[0];
      }
    }
    ;

    updateLog(logs, setLogs, gameInfo, log);
  });
  const nextActive = nextRole(params);

  if (nextActive.key === 'villager') {
    setBitedSicker(false);
    setPlayers(players.map((p) => {
      if (p.role === '') p.role = 'villager';
      return p;
    }));

    if (log.poison !== '') {
      setPlayers(players.map((p) => {
        if (p.name === log.poison) {
          p.status = false;
          newNoti(p.name + ' died by poison', params);
          checkDieImpact(p.name, params);
        }

        return p;
      }));
    }
    log.bited.forEach((bited) => {
      if (bited !== log.protect || !log.revive) {
        const player = players.find((p) => p.name === bited);

        if (player.role == 'cursed' && player.note != 'bited') {
          setPlayers(players.map((p) => {
            if (p.name === bited) p.note = 'bited';
            return p;
          }));
        } else if (player.role == 'toughguy') {
          setPlayers(players.map((p) => {
            if (p.name === bited) p.note = 'died';
            return p;
          }));
        } else {
          if (player.role == 'sicker') {
            setBitedSicker(true);
          }
          setPlayers(players.map((p) => {
            if (p.name === bited) p.status = false;
            return p;
          }));
          newNoti( bited + ' died by wolf bite', params);
          checkDieImpact(bited, params);
        }
      };
    },
    );
    checkWin(params);
  }
};

export const checkWin = (params) => {
  const { players, gameInfo, setGameInfo } = params;
  const survivalPlayers = players.filter((p) => p.status);
  const all = survivalPlayers.length;
  const wolfs = survivalPlayers.filter((p) => ['wolf', 'childwolf', 'badwolf'].includes(p.role) || (p.role === 'cursed' && p.note === 'bited')).length;
  const cupids = survivalPlayers.filter((p) => p.role === 'cupid' || p.lover).length;

  if (wolfs === 0) {
    setGameInfo({ ...gameInfo, winner: 'villager' });
    newNoti( 'Villager win', params);
    return;
  }

  if (wolfs * 2 > all || all < 3) {
    setGameInfo({ ...gameInfo, winner: 'wolf' });
    newNoti( 'Wolfgang win', params);
    return;
  }

  if (cupids * 2 > all) {
    setGameInfo({ ...gameInfo, winner: 'cupid' });
    newNoti( 'Cupid win', params);
    return;
  }
};

export const newNoti = (text, params) => {
  const { setLogs, logs, setNoti, gameInfo } = params;
  setLogs(logs.map((log, index) => {
    console.log('ggg');
    if (index === gameInfo.day) {
      log.texts.push(text);
    }
    return log;
  },
  ));

  setNoti((noti) => noti.concat(text));
};

export const checkDieImpact = (name, params) => {
  const { players, setPlayers } = params;
  checkWin(params);
  const player = players.find((p) => p.name === name);
  checkHunter(name, params);
  if (player.lover) {
    const secondLover = players.find((p) => p.name !== name && p.lover);
    newNoti(secondLover.name + ' died by love', params);
    setPlayers(players.map((p) => {
      if (p.name === secondLover.name) p.status = false;
      return p;
    }));
    checkHunter(secondLover.name, params);
  }
};

export const checkHunter = (name, params) => {
  const { players, setHunter } = params;
  const player = players.find((p) => p.name === name);
  if (player.role == 'hunter') {
    setHunter(players.filter((p) => p.status));
  }
};

export const updateLog = (logs, setLogs, gameInfo, newLog) => {
  setLogs(logs.map((log, index) => {
    if (index === gameInfo.day) {
      return newLog;
    } else {
      return log;
    }
  }));
};


export const buildRoleAction = (players, roles, gameInfo, bitedSicker) => {
  const { turn } = gameInfo;
  const role = roles[turn];
  let name = '';
  let number = 1;
  let items = players.filter((p) => p.status);
  let text = '';
  const values = [];
  let disable = false;
  switch (role.key) {
    case 'cupid':
      name = 'lover';
      number = 2;
      text = 'Lovers';
      break;
    case 'wolf':
      name = 'wolf_bite';
      text = 'Si mun cn ai';
      disable = bitedSicker;
      break;
    case 'minion':
      name = 'minion_report';
      items = players.filter((p) => p.role == 'wolf');
      number = players.filter((p) => p.role == 'wolf').length;
      text = 'Nhng ngi ny l si';
      break;
    case 'lonewolf':
      name = 'lone_wolf_bite';
      text = 'Si n c cn';
      items = players.filter((p) => p.status && p.role == 'wolf');
      break;
    case 'seer':
      name = 'indent';
      text = 'Seer indent';
      break;
    case 'bodyguard':
      name = 'protect';
      text = 'Protect';
      break;
    case 'witch':
      disable = !roles.find((r) => r.key == 'witch').poison;
      name = 'poison';
      text = 'Poison';
      break;
    case 'spellcaster':
      name = 'silent';
      text = 'Silent';
      break;
    case 'villager':
      name = 'vote';
      text = 'Vote';
      break;
    default:
      return false;
  }
  if (players.filter((p) => p.role == role.key && p.status).length == 0 && gameInfo.day !== 0 && role.key !== 'villager' && role.key !== 'wolf') {
    disable = true;
  }

  return {
    role: role.key,
    name: name,
    number: number,
    items: items,
    text: text,
    disable: disable,
    values: values,
  };
};
