import React from 'react';
import { Modal } from 'antd';
export const NotiModal = (props) => {
  const { noti, setNoti } = props;
  const closePopup = () => {
    setNoti([]);
  };
  return (
    <>
      <Modal
        open={noti.length > 0}
        footer={null}
        onCancel={closePopup}
      >
        {noti.map((text) => <h1 key={text}>{text}</h1> )}
      </Modal>
    </>
  );
};
