const LOCAL_HOST = 'localhost:3000';
const SERVER_HOST = '103.56.160.61:3000';
import axios from 'axios';

const type = 'local';

export const setURL = () => {
  let host = '';
  if (type === 'local') {
    host = LOCAL_HOST;
  } else {
    host = SERVER_HOST;
  }
  return 'http://' + host + '/api/v1/';
};

export const setCable = () => {
  let host = '';
  if (type === 'local') {
    host = LOCAL_HOST;
  } else {
    host = SERVER_HOST;
  }
  return 'ws://' + host + '/cable';
};
export const URL = setURL();
export const CABLE = setCable();


export const loginApi = async (params, callbackSuccess, callbackError) => {
  const api = URL + 'login';
  const response = await axios.post(api, params);
  if (response.data.status === 200) {
    callbackSuccess(response.data);
    sessionStorage.setItem('token', response.data.data.token);
    sessionStorage.setItem('user_id', response.data.data.user_id);
  } else {
    callbackError();
  }
};

export const createRoom = async (callbackSuccess) => {
  const api = URL + 'rooms';
  const response = await axios.post(api);
  if (response.data.status === 200) {
    callbackSuccess();
  }
};
