import React, { useState } from 'react';
import './style.scss';
export const News = () => {
  const [curPage, setCurPage] = useState(1);
  const [scrolling, setScrolling] = useState(false);
  const animTime = 1000;

  function pagination() {
    setScrolling(true);
    setTimeout(function() {
      setScrolling(false);
    }, animTime);
  };

  const className = (page) => {
    return page === curPage ? 'active' : 'inactive';
  };
  function navigateUp() {
    if (curPage === 1) return;
    setCurPage(curPage - 1);
    pagination();
  };

  function navigateDown() {
    if (curPage === 4) return;
    setCurPage(curPage + 1);
    pagination();
  };

  document.addEventListener('wheel', function(e) {
    if (scrolling) return;
    if (e.deltaY > 0) {
      navigateUp();
    } else {
      navigateDown();
    }
  });

  document.addEventListener('keydown', function(e) {
    if (scrolling) return;
    if (e.which === 38) {
      navigateUp();
    } else if (e.which === 40) {
      navigateDown();
    }
  });

  return (
    <>
      <div className="skw-pages">
        <div className={'skw-page '+ className(1)}>
          <div className="skw-page__half skw-page">
            <h1>hello Page 1</h1>
          </div>
        </div>
        <div className={'skw-page '+ className(2)}>
          <div className="skw-page__half skw-page">
            <h1>hello Page 2</h1>
          </div>
        </div>
        <div className={'skw-page '+ className(3)}>
          <div className="skw-page__half skw-page">
            <h1>hello Page 3</h1>
          </div>
        </div>
        <div className={'skw-page '+ className(4)}>
          <div className="skw-page__half skw-page">
            <h1>hello Page 4</h1>
          </div>
        </div>
      </div>
    </>
  );
};
