import React, { useState } from 'react';
import { Card } from '../components/Card';
import { Mask } from '../components/Mask';

import {
  Input,
} from 'antd';
const { Search } = Input;
export const Players = (props) => {
  const [name, setName] = useState('');
  const { players, setPlayers, gameInfo } = props;
  return (
    <>
      {gameInfo !== null && <Mask text="Game is started" />}
      <h3>Player list</h3>
      <Search
        placeholder="Input name"
        onSearch={(e) => {
          setPlayers([...players, { name: e, role: '', status: true, lover: false, note: '' }]);
          setName('');
        }}
        value={name}
        onChange={(e) => setName(e.target.value)}
        enterButton="+"
      />
      {players.map((player, index) => (
        <Card
          text={player.name}
          onClick={() => {
            setPlayers(players.filter((e) => e.name !== player.name));
          }}
          icon={''}
          key={index}
        />
      ))}
      <hr/>
    </>
  );
};
