import React, { useEffect, useState } from 'react';
import Stack from '@mui/material/Stack';
import IconButton from '@mui/material/IconButton';
import { zeroPad } from 'react-countdown';
import { Replay, Replay30, SkipNext } from '@mui/icons-material';
import { Box, Modal, Button } from '@mui/material';

const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  align: 'center',
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};

const audio = new Audio('/banana-song.mp3');

const useCountdown = (mi, setMi, audio, setTimeout) => {
  useEffect(() => {
    if (mi > 0) {
      if (mi === 1) {
        setTimeout(true);
        audio.play();
      }
      const interval = setInterval(() => {
        setMi(mi - 1);
      }, 990);
      return () => clearInterval(interval);
    }
  }, [mi]);

  return getReturnValues(mi);
};

const getReturnValues = (countDown) => {
  const minutes = Math.floor(countDown / 60);
  const seconds = Math.floor(countDown % 60);
  return [minutes, seconds];
};

const CountdownTimer = (props) => {
  const { hidden } = props;
  const [action, setAction] = useState(false);
  const [mi, setMi] = useState(0);
  const [timeout, setTimeout] = useState(false);
  const [minutes, seconds] = useCountdown(mi, setMi, audio, setTimeout);
  const display = action ? 'block' : 'none';
  const timerDisplay = hidden ? 'none' : 'block';
  useEffect(() => {
    if (timeout) {
      audio.play();
    } else {
      audio.pause();
      audio.currentTime = 0;
    }
  }, [timeout]);

  return (

    <div className='mini'>
      <Modal
        open={timeout}
        onClose={() => {
          setTimeout(false);
        }}
      >
        <Box sx={{ ...style, width: 200 }} align="center" >
          <h1>Time out</h1>
          <Button
            variant='outlined'
            onClick={() => {
              setTimeout(false);
            }}
          >
            Stop
          </Button>
        </Box>
      </Modal>
      <Stack direction="row" alignItems="center" sx={{ display: display, width: 160 }} className='clock-actions' spacing={0}>
        <IconButton variant="outlined" onClick={() => {
          setMi(180);
          setAction(false);
        }} aria-label="delete" size="large">
          <Replay fontSize="inherit" sx={{ color: 'black' }} />
        </IconButton>
        <IconButton variant="outlined" onClick={() => {
          setMi(30);
          setAction(false);
        }} aria-label="delete" size="large">
          <Replay30 fontSize="inherit" sx={{ color: 'black' }} />
        </IconButton>
        <IconButton variant="outlined" onClick={() => {
          setMi(0);
          setAction(false);
        }} aria-label="delete" size="large">
          <SkipNext fontSize="inherit" sx={{ color: 'black' }} />
        </IconButton>
      </Stack>
      <Box className='clock-value' sx={{ display: timerDisplay }} onClick={() => setAction(!action)}>
        {zeroPad(minutes)}:{zeroPad(seconds)}
      </Box>
    </div>
  );
};


export { useCountdown, CountdownTimer };
