import React, { useState } from 'react';
import {
  Button,
  Modal,
  Box,
  Switch,
  TextField,
  FormControl,
  FormGroup,
  Select,
  MenuItem,
} from '@mui/material';
import { Card } from '../components/Card';
import { DEFAULT_DESK } from '../constant';
const style = {
  position: 'absolute',
  top: '50%',
  left: '50%',
  transform: 'translate(-50%, -50%)',
  width: 400,
  bgcolor: 'background.paper',
  border: '2px solid #000',
  boxShadow: 24,
  pt: 2,
  px: 4,
  pb: 3,
};
export const Settings = (props) => {
  const { players, resetGame } = props;
  const [editModal, setEditMoal] = useState(false);
  const [info, setInfo] = useState({});

  return (
    <>
      <Button
        variant='outlined'
        onClick={resetGame}
      >
        Reset
      </Button>
      <hr/>
      { players.map((player) =>
        <Card
          key={player.id}
          icon={player.role}
          text={player.name}
          lover={player.lover}
          onClick={() => {
            setEditMoal(true);
            setInfo(player);
          }}
        />,
      )}
      <Modal
        open={editModal}
        onClose={() => setEditMoal(false)}
      >
        <Box sx={{ ...style, width: 300 }}>
          <FormControl>
            <FormGroup>
              Status
              <Switch
                checked={info.status}
                onClick={() => {
                  setInfo({ ...info, status: !info.status });
                }}
              />
            </FormGroup>
            <FormGroup>
              <TextField
                label="Name"
                variant="outlined"
                onChange={(e) => setInfo({ ...info, name: e.target.value })}
                value={info.name}
              />
            </FormGroup>
            <FormGroup>
              <Select
                onChange={(e) => {
                  setInfo({ ...info, role: e.target.value });
                }}
                value={info.role}
              >
                {DEFAULT_DESK.map((role) =>
                  <MenuItem value={role.key} key={role.key}>{role.name}</MenuItem>,
                )}


              </Select>
            </FormGroup>
            <Button
              variant='outlined'
              onClick={() => {
                setEditMoal(false);
              }}
            >
              Save
            </Button>
          </FormControl>
        </Box>
      </Modal>

    </>
  );
};
