import React, { useState, useEffect } from 'react';

import { Review } from './components/Review';
import './index.css';

export const Tool = () => {
  const [output, setOutput] = useState('');
  const [imagePreviewUrl, setImagePreviewUrl] = useState(null);
  const [colors, setColors] = useState([]);
  const [file, setFile] = useState();
  const [map, setMap] = useState([]);
  const [open, setOpen] = useState(false);
  const [transparent, setTransparent] = useState({ x: null, y: null });
  const [height, setHeight] = useState(10);
  const [width, setWidth] = useState(10);

  const colorToChar = (color) => {
    const tmp = colors;
    if (!colors.includes(color)) {
      tmp.push(color);
      setColors(tmp);
    };

    return String.fromCharCode(65 + colors.indexOf(color));
  };
  const renderColorUsed = () => {
    let text = '';
    colors.map((color, index) => {
      text += `  ["${String.fromCharCode(65 + index)}"] = { fg = "${color}" },\n`;
    });
    return text;
  };
  const hexToRgb = (hex) => {
    return hex.replace(/^#?([a-f\d])([a-f\d])([a-f\d])$/i
        , (m, r, g, b) => '#' + r + r + g + g + b + b)
        .substring(1).match(/.{2}/g)
        .map((x) => parseInt(x, 16));
  };

  const allowableColor = (color) => {
    let result = color;
    const rgbColor = hexToRgb(color);
    colors.forEach((item) => {
      const rgbItem = hexToRgb(item);
      if (compare(rgbColor, rgbItem)) {
        result = item;
      }
    });
    return result;
  };

  const compare = (rgbColor, rgbItem) => {
    const range = 6;
    return rgbColor[0] > rgbItem[0] - range && rgbColor[0] < rgbItem[0] + range &&
    rgbColor[1] > rgbItem[1] - range && rgbColor[1] < rgbItem[1] + range &&
    rgbColor[2] > rgbItem[2] - range && rgbColor[2] < rgbItem[2] + range;
  };

  const pickColor = (x, y, img, ctx) => {
    const existColor = [];
    for (let xPoint = img.width/width*x; xPoint < img.width/width*(x+1); xPoint++ ) {
      for (let yPoint = img.height/height*y; yPoint < img.height/height*(y+1); yPoint++ ) {
        const pixelData = ctx.getImageData(xPoint, yPoint, 1, 1).data;
        const r = pixelData[0];
        const g = pixelData[1];
        const b = pixelData[2];
        existColor.push(rgbToHex(r, g, b));
      }
    }
    const pickedColor = mode(existColor);
    return allowableColor(pickedColor);
  };

  const mode = (myArray) => {
    return myArray.reduce(
        (a, b, i, arr)=>
          (arr.filter((v)=>v===a).length>=arr.filter((v)=>v===b).length?a:b),
        null);
  };

  const handleClose = (height, width, transparent) => {
    setOpen(false);
    const reader = new FileReader();
    if (file) {
      reader.readAsDataURL(file);
      reader.onload = (event) => {
        const img = new Image();
        img.onload = () => {
          const canvas = document.createElement('canvas');
          const ctx = canvas.getContext('2d');

          canvas.width = img.width;
          canvas.height = img.height;
          ctx.drawImage(img, 0, 0);

          const resultMap = [];
          for (let y = 0; y < height; y++) {
            let row = '';
            for (let x = 0; x < width; x++) {
              const hexColor = pickColor(x, y, img, ctx);
              const char = colorToChar(hexColor);
              row += char;
            }
            resultMap.push(`[[${row}]]`);
          }
          setMap(resultMap);
        };
        img.src = event.target.result;
      };
    }
  };

  const handleShowImage = (e) => {
    const file = e.target.files[0];
    setImagePreviewUrl(URL.createObjectURL(file));
    setFile(file);
    setColors([]);
    setOpen(true);
  };
  useEffect(() => {
    if (map.length !== 0) {
      console.log(map);
      let transChar = '-';
      if (transparent.x !== null) {
        transChar = map[transparent.y][transparent.x];
      }
      const outputString = `
local M = {}
M.map = {
${map.join(',\n').replaceAll(transChar, ' ' )};
},
}

M.colors = {
${renderColorUsed()}
}
return M;
`;
      setOutput(outputString);
    }
  }, [map]);

  const rgbToHex = (r, g, b) => {
    return '#' + ((1 << 24) + (r << 16) + (g << 8) + b).toString(16).slice(1);
  };

  return (
    <div>
      <h2>Pixel Art to Lua Code Converter</h2>
      <input type="file" accept="image/*" onChange={handleShowImage} />
      <hr />
      <Review
        open={open}
        imagePreviewUrl={imagePreviewUrl}
        handleClose={handleClose}
        transparent={transparent}
        setTransparent={setTransparent}
        height={height}
        width={width}
        setHeight={setHeight}
        setWidth={setWidth}
      />

      <hr />
      <textarea
        value={output}
        readOnly
        rows="50"
        cols="100"
        style={{ whiteSpace: 'pre' }}
      />
    </div>
  );
};

