import React from 'react';
import {
  IconButton,
  AppBar,
  Toolbar,
} from '@mui/material';
import { Memory, ManageAccounts, Flaky, EventNote, Settings } from '@mui/icons-material';
export const Header = (props) => {
  const { setTab, tab } = props;
  const navs = [
    {
      name: 'home',
      icon: <Memory />,

    },
    {
      name: 'players',
      icon: <ManageAccounts />,

    },
    {
      name: 'roles',
      icon: <Flaky />,

    },
    {
      name: 'notes',
      icon: <EventNote />,

    },
    {
      name: 'settings',
      icon: <Settings />,

    },
  ];
  return (
    <AppBar
      position="fixed"
      sx={{
        bgcolor: 'white',
        boxShadow: 0,
        borderBottom: 1,
        borderColor: 'black',
        height: '50px',
      }}
    >
      <Toolbar
        sx={{
          padding: 0,
        }}
      >
        {navs.map((item) => (
          <>
            {item.name === tab &&
              <IconButton
                key={item.name}
                sx={{
                  color: 'black',
                  width: '20%',
                  height: '43px',
                  borderColor: 'black',
                  border: 1,
                  borderBottomColor: 'white',
                  borderRadius: '8px',
                  borderBottomLeftRadius: 0,
                  borderBottomRightRadius: 0,
                }}
                variant="outlined"
                onClick={() => setTab(item.name)}
              >
                {item.icon}
              </IconButton>
            }
            {item.name !== tab &&
              <IconButton
                key={item.name}
                sx={{ color: 'black', width: '20%', height: '43px' }}
                onClick={() => setTab(item.name)}
              >
                {item.icon}
              </IconButton>
            }
          </>

        ))}
      </Toolbar>
    </AppBar>
  );
};
