import React from 'react';
import { Button, Grid } from '@mui/material';
import DoNotDisturbAltRoundedIcon from '@mui/icons-material/DoNotDisturbAltRounded';
import { FavoriteBorder } from '@mui/icons-material';
const Icon = ({ type, active }) => {
  const iconColor = active ? 'white' : 'black';
  switch (type) {
    case 'none':
      return (
        <DoNotDisturbAltRoundedIcon />
      );
      break;
    case '':
      return (
        <Grid />
      );
      break;
    case null:
      return (
        <Grid />
      );
      break;
    case undefined:
      return (
        <Grid />
      );
      break;
    default:
      return (
        <img
          src={'/werewolf_icon/'+ iconColor+ '/' + type + '.png'}
          height={20}
        />
      );
  }
};
const LoverIcon = ({ lover }) => {
  if (lover) {
    return (
      <FavoriteBorder
        sx={{
          position: 'absolute',
          top: 0,
          right: 0,
          fontSize: 18,
          color: 'pink',
        }}
      />
    );
  } else {
    return (
      <Grid />
    );
  }
};
export const Card = (props) => {
  const { onClick, text, active, disable, icon, note, lover } = props;
  const type = active ? 'contained' : 'outlined';
  let color = active ? 'white' : 'black';
  if (note === 'bited' || ['lycan', 'wolf', 'lonewolf', 'childwolf', 'bigbadwolf'].includes(icon)) color = 'red';
  const bg = active ? 'black' : 'white';
  return (
    <Button
      variant={type}
      onClick={onClick}
      disabled={disable}
      style={{
        textTransform: 'none',
        height: 50,
        width: 85,
        margin: 5,
        backgroundColor: bg,
        padding: 0,
        color: color,
        borderColor: color,
      }}
      startIcon={
        <Icon
          type={icon}
          active={active}
        />
      }
    >
      {text}
      <LoverIcon
        lover={lover}
      />
    </Button>
  );
};

