import React, { useEffect } from 'react';
// import moment from 'moment';
import { Grid } from '@mui/material';
export const Notes = (props) => {
  const { logs, players, gameInfo } = props;
  useEffect(() => {
    console.log(logs);
    copyText();
  }, [logs]);
  const copyText = () => {
    let text = '';
    text = text + 'Phe chin thng: ' + gameInfo.winner + '\n';
    players.forEach((player) => {
      text = text + player.name + ': ' + player.role;
      text = text + '\n';
    });
    logs.forEach((log) => {
      text = text + log.texts.join('\n');
    });
    copyToClipboard('```'+text+'```');
  };


  const copyToClipboard = (content) => {
    if (window.isSecureContext && navigator.clipboard) {
      navigator.clipboard.writeText(content);
    } else {
      unsecuredCopyToClipboard(content);
    }
  };

  const unsecuredCopyToClipboard = (text) => {
    const textArea = document.createElement('textarea');
    textArea.value=text; document.body.appendChild(textArea);
    textArea.focus();
    textArea.select();
    try {
      document.execCommand('copy');
    } catch (err) {
      console.error('Unable to copy to clipboard');
    }
    document.body.removeChild(textArea);
  };

  return (
    <>
      {players.map((player) =>
        <h3 key={player.name}>{player.name}: {player.role}</h3>,
      )}
      {logs.map((log, index) =>
        <>
          <hr/>
          <Grid key={index}>
            <h2>Ngay thu {index + 1}</h2>
            {log.bited.map((bited) =>
              <h3 key={bited}>Soi can: {bited}</h3>,
            )}
            {log.protect !== '' && <h3>Bao ve: {log.protect}</h3>}
            {log.indent !== '' && <h3>Tien tri soi: {log.indent}</h3>}
            {log.revive !== '' && <h3>Phu thuy cuu: {log.revive}</h3>}
            {log.poison !== '' && <h3>Phu thuy ha doc: {log.poison}</h3>}
            {log.silent !== '' && <h3>Phap su lam cam: {log.silent}</h3>}
            {log.texts.map((text) =>
              <h3 key={text}>{text}</h3>,
            )}
          </Grid>
        </>,
      )}
    </>
  );
};
