import React from 'react';

import { Modal, Box, Button, TextField } from '@mui/material';
export const Review = (props) => {
  const { open, handleClose, imagePreviewUrl, transparent, setTransparent, height, width, setHeight, setWidth } = props;
  const style = {
    position: 'absolute',
    top: '50%',
    left: '50%',
    transform: 'translate(-50%, -50%)',
    width: 1000,
    height: '80vh',
    bgcolor: 'background.paper',
    border: '2px solid #000',
    boxShadow: 24,
    overflow: 'auto',
    p: 4,
  };
  const renderRow = (y) => Array.from({ length: width }, (_, index) => (
    <td key={index} onClick={() => (transparent.x === index && transparent.y === y) ? setTransparent({ x: null, y: null }) : setTransparent({ x: index, y: y }) }>
      {(index === transparent.x && y === transparent.y) &&
        <div style={{ width: '0px', height: '0px' }}>
        hi
        </div>}
    </td>
  ));
  const renderGrid = Array.from({ length: height }, (_, index) => (
    <tr key={index}>{renderRow(index)}</tr>
  ));
  return (
    <Modal
      open={open}
      aria-labelledby="modal-modal-title"
      aria-describedby="modal-modal-description"
    >
      <Box sx={style}>
        <Box style={{ border: 'solid 1px', position: 'relative' }}>
          <img src={imagePreviewUrl} alt="Preview" style={{ width: '100%', height: 'auto' }} />
          <Box style={{ position: 'absolute', top: 0, width: '100%', height: '100%' }}>
            <table style={{ width: '100%', height: '100%' }} >
              <tbody>
                {renderGrid}
              </tbody>
            </table>
          </Box>
        </Box>
        <Box>
          <Box>
            Height:
            <TextField
              value={height}
              variant="outlined"
              type="number"
              sx={{
                padding: 0,
              }}
              onChange={(e) => setHeight(e.target.value)} />
          </Box>
          <Box>
            Width:
            <TextField
              value={width}
              variant="outlined"
              type="number"
              sx={{
                padding: 0,
              }}
              onChange={(e) => setWidth(e.target.value)} />
          </Box>
        </Box>
        <hr/>
        <Button variant="outlined" onClick={() => handleClose(height, width, transparent)}>Ok</Button>
      </Box>
    </Modal>

  );
};
