import React, { useState } from 'react';
import { Button, Box } from '@mui/material';
import { Clock } from './components/Clock';
const style = {
  fontSize: 30,
  py: 2,
};
export const OneNight = () => {
  const [mode, setMode] = useState(false);
  const [open, setOpen] = useState(false);
  const [arr, setArr] = useState(
      [
        {
          text: 'Nhn bn',
          active: true,
        },
        {
          text: 'Si',
          active: true,
        },
        {
          text: 'K phn bi - Minion',
          active: true,
        },
        {
          text: 'Tin tri',
          active: true,
        },
        {
          text: 'K ph ri - i bi',
          active: true,
        },
        {
          text: 'Say ru',
          active: true,
        },
        {
          text: 'Nhn bn',
          active: true,
        },
        {
          text: 'Con bnh - Mt ng',
          active: true,
        },
      ],
  );

  const clickItem = (text) => {
    if (mode) {
      setArr(arr.map((e) => {
        const newE = e;
        if (e.text == text) newE.active = !newE.active;
        return newE;
      }));
    }
  };
  return (

    <Box sx={{ px: 3 }}>
      <Clock
        open={open}
        setOpen={setOpen}
      />
      <Button
        variant="outlined"
        onClick={() => {
          setMode(!mode);
        }}
      >
        {mode ? 'Editing ' : 'Locking'}
      </Button>
      <Button
        variant="outlined"
        onClick={() => {
          setOpen(true);
        }}
      >
        Clock
      </Button>
      <hr/>
      {arr.map((item) => (
        <>
          {
            item.active && <Box sx={style} key={item.text} onClick={() => clickItem(item.text)}>{item.text}</Box>
          }
        </>
      ),
      )}
      <hr/>
      {arr.map((item) => (
        <>
          {
            !item.active && <Box sx={{ ...style, color: mode ? 'black': 'white' }} key={item.text} onClick={() => clickItem(item.text)}>{item.text}</Box>
          }
        </>
      ),
      )}
    </Box>
  );
};
